.runningLineContainer {
  overflow: hidden;
}
.runningLine {
  white-space: nowrap;
  display: block;
  position: relative;
  height: 30px;
  letter-spacing: 3px;
  margin-top: 10px;
  animation: scroll 150s linear infinite;
  animation-play-state: running;
  cursor: default;
}
.runningLine:hover {
  animation-play-state: paused; /* Pause the animation on hover */
}
.nickname {
  /* font-style: italic; */
  font-size: 16px;
  font-weight: 200;
  letter-spacing: 3px;
  cursor: pointer;
  color: var(--text-color);
  text-decoration: none;
}
.nickname:hover {
  text-decoration: underline;
  color: var(--accent-color);
}

.recruitment,
.clanWars {
  color: var(--accent-color);
  font-weight: 700;
  text-decoration: none;
  cursor: default;
}

@keyframes scroll {
  from {
    transform: translateX(-10%); /* Start from the very left */
  }
  to {
    transform: translateX(-200%);
  }
}

@media (max-width: 829px) {
  @keyframes scroll {
    from {
      transform: translateX(30%); /* Start from the very left */
    }
    to {
      transform: translateX(-400%);
    }
  }
  .runningLine {
    font-size: 12px;
    letter-spacing: 1px;
    animation: scroll 40s linear infinite;
  }
  .nickname {
    /* font-style: italic; */
    font-size: 12px;
  }
}
